.justifyStart {
  -ms-flex-pack: start;
      justify-content: flex-start;
}

.list-item {
    border: 1px solid #ccc;
    padding: 5px 10px;
    margin-bottom: 15px;
}

.list-item .delete-icon {
    float: right;
    background-color: #333333;
    width: 20px;
    height: 20px;
    line-height: 20px;
    text-align: center;
    font-size: 12px;
    color: #ffffff;
    border-radius: 50px;
    cursor: pointer;
}

.thumbnail-image {
    max-width: 150px;
}



